import Vue from 'vue'
import Vuex from 'vuex'
import VuexPersistence from 'vuex-persist'
const vuexLocal = new VuexPersistence({
  storage: window.localStorage
})
Vue.use(Vuex)
export default new Vuex.Store({
    state: {
      // 登陆注册
   obj:{
     username:"", 
     user_id:"",
     login:false,
    
   },
  //  总数
   allNum:"",
  // 总价
  allPrice:"",
  //  地址
   arr:[],
  //  购物车
   list:[],
  //  头像
  img:"",
  order:[]
    },
    mutations:{
      // 登陆
      add(state,val){
        state.obj=val
      },
      // 数量添加
      addNum(state,item){
        state.list.map((i,index)=>{
          if(i.id==item.id){
            i.num++
          }
        })
      
      },
      jianNum(state,item){
        state.list.map((i,index)=>{
          if(i.id==item.id){
            i.num--
            if(i.num<=0){
            state.list.splice(i,1)
            i=i++
            }
          }
        
        })
      
      },
      // 购物车
      addList(state,val){
        console.log( state.list)
        console.log(val)
        var bool=true
      state.list.map((item,index)=>{
        if(item.name==val.name&&item.xuan==val.xuan){
          item.num+=1
          bool=false
        }
      })
      if(bool==true){
        val.num=1
        state.list.push(val)
      }
      },
      // 添加地址
      addArr(state,val){
        state.arr.push(val)
      },
      // 修改img
      gaiImg(state,val){
        state.img=val
      },
      // 退出登录
      tui(state){
        state.obj={
          username:"", 
          user_id:"",
          login:false
        }
      },
      // 修改地址
      xiu(state,index){
        state.arr.splice(index,1)
      },
      // 清空
      remove(state,val){
        state.list=val
      },
      order(state,val){
        state.order.push(val)
      }
      
    },
    plugins: [vuexLocal.plugin],
    getters:{
     
    }
  })


