import Vue from 'vue'
import Router from 'vue-router'
const Home = () => import('../page/Home/Home')
// import Main from '../page/Home/main'
const Login = () => import('../page/Home/detail/login')

// import Login from "../page/Home/detail/login"

const Forget = () => import('../page/Home/detail/forget')

// import Forget from "../page/Home/detail/forget"
const Detail = () => import('../page/Home/detail/detail')

// import Detail from "../page/Home/detail/detail"
// const Header = () => import('../page/nearby/header')


// import Foot from  "../components/foot"

const TakeOut = () => import('../page/nearby/take-out')

// import TakeOut from "../page/nearby/take-out"

const Banner = () => import('../components/banner')

// import Banner from "../components/banner"

const Search = () => import('../page/nearby/find')

// import Find from "../page/nearby/find"

const My = () => import('../page/nearby/my')

// import My from "../page/nearby/my"

const Ding = () => import('../page/nearby/ding')
const Info = () => import('../page/nearby/Info')
const Shop = () => import('../page/nearby/shop')
const SetUserName = () => import('../page/nearby/setusername')
// import Ding from "../page/nearby/ding"
const App = () => import('../App')
const Cd  =()=> import ('../page/nearby/cd')
// import App from "../App"
const Address  =()=> import ('../page/nearby/address')
Vue.use(Router)
const AddressChild=()=>import("../page/nearby/addressChild/address")
const Addressadd=()=>import("../page/nearby/addressChild/addressadd")

const AddAddress=()=>import("../page/nearby/addressChild/address_Add")
const Addressadd2=()=>import("../page/nearby/addressChild/addressadd2")







const OrderPay=()=>import('../page/nearby/Order-pay')
export default new Router({
  routes: [{
    path:'/',
    name:App,
    component:App,
    redirect:'/home',
    children:[
      {
        path:'/OrderPay',
        name:'OrderPay',
        component:OrderPay
      },
      {
        path: '/home',
        name: 'Home',
        component: Home,
      
      },
      {
        path: '/cd',
        name: 'Cd',
        component: Cd,
        meta:{
          keepAlive:true
        },
      
      },
      {
        path: '/login',
        name: 'Login',
        component: Login,
     
      },
      {
        path: '/forget',
        name: 'Forget',
        component: Forget,
      
      },
      {
        path: '/detail/:id',
        name: 'Detail',
        component: Detail,
      },
          {
            path: '/takeout',
            name: 'TakeOut',
            component: TakeOut,
            
            children:[
              {
                path: 'banner',
                name: 'Banner',
                component: Banner,
              }
            ]
          },
          {
            path: '/my',
            name: 'My',
            component: My,
           
          },
          {
            path: '/ding',
            name: 'Ding',
            component: Ding,
          },
          {
            path: '/search',
            name: 'Search',
            component: Search,
          },
          {
            path: '/Info',
            name: 'Info',
            component: Info,
          },
          {
            path: '/shop',
            name: 'Shop',
            component: Shop,
          },
       
            {
              path: '/setusername',
              name: 'SetUserName',
              component: SetUserName
            },
            
            {
              path: '/address',
              name: 'address',
              component: Address,
              redirect:"/address/addressChild",
              children:[
                {
                  path: 'addressChild',
                  name: 'addressChild',
                  component: AddressChild,
                
                },
                {
                  path: 'addaddress',
                  name: 'AddAddress',
                  component: AddAddress,
                 
                  children:[
                    {
                      path: '/address/addaddress/',
                      name: 'Addressadd',
                      component: Addressadd,
                      meta:{
                        keepAlive:true
                      },
                    },
                    {
                      path: 'addressaddA',
                      name: 'Addressadd2',
                      component: Addressadd2,
                    }
                  ]
                },
                
                
              ]
            }
        ]
      }]
    })
  



