<template>
  <div id="app">
    <transition name="fade">
        <keep-alive>  
   <router-view  v-if="$route.meta.keepAlive"/>
</keep-alive>
    </transition>
<transition name="fade">
   <router-view v-if="!$route.meta.keepAlive"/>
</transition>
  
   
  </div>
</template>

<script>
export default {
  name: 'App',
  data(){
    return{
    }
  }
}
</script>

<style>
    .fade-enter,.fade-leave-to{
      opacity: 0;
    }
    .fade-enter-active,.fade-leave-active{
          transition: all 1.5s;
    }
</style>



